#body {
  transition: opacity 0.2s; }

#DocumentContainer {
  margin: 0 auto;
  max-width: 60%;
  min-width: 800px;
  min-height: 300px;
  background-color: #222;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  margin-bottom: 50px; }
  #DocumentContainer #ImageContainer {
    width: 100%;
    padding-bottom: 50%;
    background-color: #eee;
    position: relative; }
    #DocumentContainer #ImageContainer #DocumentImage {
      width: 100%;
      min-width: 50px;
      align-self: center;
      position: absolute; }
  #DocumentContainer #DocumentText {
    color: white;
    padding: 30px;
    width: 100%; }
    #DocumentContainer #DocumentText h3 {
      font-weight: 700; }
    #DocumentContainer #DocumentText h6 {
      color: #ccc;
      font-weight: 700;
      font-size: 14px; }

@media (max-width: 800px) {
  #DocumentContainer {
    width: 100%;
    max-width: auto;
    min-width: 100%; } }
